"use client";

import { createContext, useContext, useState, useEffect, ReactNode } from "react";

interface AuthContextType {
  user: { name: string; email: string; balance?: number } | null;
  login: (userData: { name: string; email: string; balance?: number }) => void;
  logout: () => void;
}

const AuthContext = createContext<AuthContextType>({
  user: null,
  login: () => {},
  logout: () => {},
});

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<{ name: string; email: string; balance?: number } | null>(null);

  useEffect(() => {
    const savedUser = localStorage.getItem("user");
    if (savedUser) {
      setUser(JSON.parse(savedUser));
      console.log("ðŸ”¹ SessÃ£o restaurada:", JSON.parse(savedUser));
    }
  }, []);

  const login = (userData: { name: string; email: string; balance?: number }) => {
    setUser(userData);
    localStorage.setItem("user", JSON.stringify(userData));
    console.log("âœ… SessÃ£o atualizada:", userData);
  };

  const logout = () => {
    setUser(null);
    localStorage.removeItem("user");
    console.log("ðŸ”¹ Logout realizado");
  };

  return <AuthContext.Provider value={{ user, login, logout }}>{children}</AuthContext.Provider>;
};

export const useAuth = () => useContext(AuthContext);
