import { useState, useEffect } from "react";
import Link from "next/link";

export default function Navbar() {
  const [cartCount, setCartCount] = useState(0);

  const updateCartCount = () => {
    const savedCart = JSON.parse(localStorage.getItem("cart") || "[]");
    setCartCount(savedCart.length);
  };

  useEffect(() => {
    updateCartCount(); // inicial
    window.addEventListener("storage", updateCartCount); // atualiza se outro componente mudar

    return () => window.removeEventListener("storage", updateCartCount);
  }, []);

  return (
    <nav className="w-full bg-gray-800 p-4 shadow-md fixed top-0 left-0 z-50 flex justify-between items-center h-20">
      <h1 className="text-2xl font-bold text-white">
        <Link href="/">GamersClub</Link>
      </h1>

      <div className="flex items-center gap-6">
        {/* Carrinho */}
        <Link href="/cart" className="relative text-white">
          ðŸ›’
          {cartCount > 0 && (
            <span className="absolute -top-2 -right-3 bg-red-600 text-white text-xs font-bold px-2 rounded-full">
              {cartCount}
            </span>
          )}
        </Link>
      </div>
    </nav>
  );
}
